html {
  background-color: #24006b;
}



.container {
  border: 2px solid #7d4b9e;
  border-radius: 8px;
  width: 40vw;
  height: 50vh;
  margin: 5% auto;
  padding: 1%;
  text-align: center;
  background-color: #fff;
}

.color-container{
  width: 50%;
  height: 50%;
  border: 1px solid #111;
  border-radius: 8px;
  margin: 0 auto;
  background-color: #fff;
}

.name-container{
  width: 25%;
  height: 10%;
  border: 1px solid #111;
  background-color: #fff;
  border-radius: 8px;
  margin: 2% auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.color {
  font-size: 2rem
}

.generate {
  margin: 0 auto;
  background-color: rgb(107, 222, 107);
  padding: 1%;
  border-radius: 8px;
  border: 1px solid #6f6868;
  width: 8rem;
}

.generate:hover {
  cursor: pointer;
  background-color: rgb(36, 221, 36);
}

@media only screen and (max-width: 480px) {
  .container {
    width: 80vw;
    height: 70vh;
    margin: 10% auto;
  }

  .color-container {
    width: 80%;
    height: 40%;
  }

  .name-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 8%;
    margin: 4% auto;
  }

  .color{
    font-size: 1.4em;
  }

  button {
    width: 40%;
    height: 10%;
    font-size: 1.3rem;
  }
}
